.header {
  background-color: #3a0b18;
  text-align: center;
  padding: 1rem;
  text-align: center;
  color: #FFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  letter-spacing: 1px;
  line-height: 2;
}

h1 {
  background-image: url(https://media.giphy.com/media/l4pTa9C9WlTEfTfOM/giphy.gif);
  background-size: cover;
  color: transparent;
  font-size: 6rem;
  font-family: 'Architects Daughter', cursive, sans-serif;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  font-size: 120px;
  margin: 10px 0;

  @media screen and (max-width: 768px) {
    font-size: 3.5rem;
  }
}

.subheading {
  width: 80%;
  font-size: 2rem;
  margin: auto;
  padding: 1rem 0.5rem;


  &__text {
    text-align: center;
  }



  @media screen and (max-width: 710px) {
    font-size: 1.5rem;
    margin-top: 1rem;
  }
}



.fadeOut_1 {
  animation-name: fadeOut_1;
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}

@keyframes fadeOut_1 {
  0% {
    opacity: 1;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 0;

  }
}

.fadeOut_2 {
  animation-name: fadeOut_2;
  animation-duration: 4s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}

@keyframes fadeOut_2 {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 0;
  }

  55% {
    opacity: 1;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.fadeIn {
  animation-name: fadeIn;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}